#ui-panel {
	position: fixed;
	bottom: 0;
	border-radius: 0;
	color: white;
	background-color: #333333;
	opacity: 0.9;
	width: 100vw;
	transition: all 0.7s;
}

#ui-panel:hover {
	opacity: 1;
}

.action-button {
	color: #a7a7a7;
	border-color: #a7a7a7;
	border-radius: 0;
	margin-top: 5px;
}

.action-button:first-child {
	margin-right: 5px;
}

.action-button:hover {
	color: #ffffff;
	border-color: #949494;
	background-color: #949494;
}
